@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@700&&family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap&family=Exo+2:wght@300;400');


*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/*------------- navbar ---------------*/
#logo{
  height: 80px;
  
}

.navbar{
  background-color: rgb(255, 255, 255) !important;
  font-family: 'Poppins', sans-serif;
  font-size: 23px;
  line-height: 26px;
  text-transform: uppercase;
  box-shadow: 0 10px 6px -6px grey;
  --webkit-box-shadow: 0 8px 6px -6px grey;
  border-bottom: 0px solid grey !important;
  position: sticky;
  top: 0;
  z-index: 999;
}

.nav-link{
    font-weight: 300 !important;
}

.dropdown {
  position: relative;
  display: inline-block;
}

.dropdown-content {
  
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 240px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 1;
}

.dropdown-content a {
  color: black;
  padding: 12px 16px;
  text-decoration: none;
  display: block;
}

.dropdown-content a:hover {background-color: #514e4e57;}

.dropdown:hover .dropdown-content {display: block;}

#icon-c{
  text-decoration: none;
  color: #535151;
}

.navbar-brand {
    font-weight: 700;
    font-family: "roboto", sans-serif;
    letter-spacing: 5px;
    font-size: 25px;
    margin-left: 10px;
  }

  @media only screen and (min-width:300px){
    .navbar-toggler{
      margin-bottom:20px ;
      margin-left: 10px;
    }  
   .nav-contents{
    margin: auto;
    padding: 20px;
   }
  }


  /* -------------------carousel ------------------------*/
  .box-1 {
    background-image: url(./images/bg1.jpg);
    height: 450px;
    background-repeat: no-repeat;
    background-size: 100%;
    animation: change 10s infinite ease-in-out;
  }
  
  @keyframes change {
    0% {
      background-image: url(./images/bg4.jpg);
    }
  
    40% {
      background-image: url(./images/bg2.jpg);
    }
  
    80% {
      background-image: url(./images/bg3.jpg);
    }
  
  
  }
  /*----------------- Footer--------------- */

  .footer {

    color: white;
    padding: 25px;
    background: #000000;
    /* fallback for old browsers */
    background: -webkit-linear-gradient(to right,
        #434343,
        #000000);
    /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to right, #434343, #000000);
  }
  
  .footer Link {
    text-decoration: none;
    color: white;
  }
  
  .footer Link:hover {
    color: #ffefba;
    border-bottom: 1px solid #ffefba;
  }
  
  /*---------- Page not found---------- */

  .pnf {
    display: flex;
    min-height: 65vh;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  
  .pnf-title {
    font-size: 100px;
    font-weight: 700;
  }
  
  .pnf-heading {
    font-weight: normal;
  }
  
  .pnf-btn {
    color: black;
    border: 1px solid black;
    text-decoration: none;
    padding: 10px;
    margin-top: 10px;
  }
  
  .pnf-btn:hover {
    background-color: black;
    color: white;
  }

  /* ------------------contact us---------------- */

  .contactus {
    margin: 0;
    padding: 0;
    height: 70vh;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
   /* -----------------register page------------------ */

  .register {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 70vh;
    flex-direction: column;
  }

  
 /* ----------------------Admin Product page-------------------- */

 .product-link {
  text-decoration: none !important;
  color: black !important;
}

.cat-btn {
  padding: 40px 0px;
  font-size: 24px;
  text-transform: uppercase;
}

.cat-btn:hover {
  background-color: #434343;
  color: white;
}

#product {
  margin: 33px;
}


/*----------------------- product detail page----------------------- */

#prod-detail img{
  height: 350px;
  width: 350px;
  margin-left: 70px;
  border: 1px solid black;
}

#similar-prod{
 margin-top: 100px;
 margin-left: 30px;
 margin-bottom: 50px;
}
#similar-prod .card{
  width: 16.5rem;
}
#similar-prod img{
  height: 220px;
  padding: 8px;
}
.s-container{
  display: flex;
}
/* ------------------home page button ----------------------- */
#mob-load{
  display: none;
}

/* -------------dashboard user details---------------- */
#card{
  margin-left: 30px;
  width: 700px;
  height: 300px;
  display: flex;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 20px;
  padding: 30px;
}
#card h3{
  margin-top: 15px;
  font-family: 'Exo-2';
  font-weight: bold;
}
#card span{
  font-size: 30px;
  font-weight: 100;
  color:#434343;
}

#card img{
  width: 230px;
  margin-left: 100px;
}

/* ---------user menu--------- */
.menu-item{
  background-color: darkslategray;
  border-radius: 5px;
  text-decoration: none;
  display: block;
  width: 300px;
  height: 40px;
  text-align: center;
  padding: 5px;
  margin: 2px;
  font-family: 'playfair';
  font-size: 20px;
}
.dashboard-home{
  display: flex;
}

/* --------------orders page------------- */
#orders{
  display: flex;
}
#orders-table{
  margin-left: 70px;
}
#th2{
  display: none;
}
#th2 span{
   margin-left: 25px;
   
}
/* -------------------responsiveness -----------------------*/

@media only screen and (max-width:1000px){
  /* carousel */
  .box-1{
    height:200px;
  }
  /* load button on home page */
  #load{
   display: none;
  }
  #mob-load{
    display: inline-block;
    margin-left: 40px;
  }

  /* product detail page */
  #prod-detail img{
    width: 250px;
    height: 250px;
  }
   #detail{
    margin: 20px;
   }
   #similar-prod{
    margin-top: 70px;
   }
  .s-container{
    display: block;
  }

  /* dashboard page */
  .menu-item{
    display: inline;
  }
  .dashboard-home{
    display: block;
  }
  #card{
    margin-top: 50px;
    margin-left: 0px;
    width: 350px;
    height: 200px;
    display: flex;
    padding: 20px;
  }
  #card h3{
    margin-top: 15px;
    font-size: small;
  }
  #card img{
    width: 120px;
    height: 120px;
    margin-left: 20px;
  }
  /* orders page */
  #orders{
    display: block;
  }
  #orders-table{
    margin-left: 0px;
  }
  #orders-container{
    margin-top: 40px;
  }
  #th {
    display: none;
  }
  #tr{
    display: none;
  }
  #th2{
    display: block;
  }
}