@import url('https://fonts.googleapis.com/css2?family=Playfair+Display:wght@700&family=Poppins:wght@300;400&display=swap');

/* --------------register page------------- */
.R-container{
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 93vh;
  flex-direction: column;
  background: rgb(255,192,203);
  background: linear-gradient(90deg, rgba(255,192,203,1) 26%, rgba(224,255,255,1) 69%, rgba(224,255,255,1) 69%); 
}

  .main {
    margin: auto;
    margin-top: 50px;
    margin-bottom: 50px;
    background-color: black;
    width: 500px;
    height: 600px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 5px 20px 50px #000;
  }


.signup {
  position: relative;
  width: 100%;
  height: 100%;
}

.s-label {
  color:rgb(148, 209, 209);
  font-size: 2.1em;
  justify-content: center;
  display: flex;
  margin: 10px;
  font-weight: bold;
  font-family:'Noto Sans Display', sans-serif;
  cursor: pointer;
  transition: .5s ease-in-out;
}

.s-input {
  color: #fff;
  width: 60%;
  height: 45px;
  background-color: #000;
  border: 1px solid #fff;
  justify-content: center;
  display: flex;
  margin: 20px auto;
  padding: 10px;
  border-radius: 5px;
}

.s-textarea {
  color: #fff;
  width: 60%;
  height: 80px;
  background-color: #000;
  border: 1px solid #fff;
  border-radius: 5px;
  justify-content: center;
  display: flex;
  margin: 20px auto;
  padding: 10px;
}

.sign-btn {
  width: 60%;
  height: 40px;
  margin: 10px auto;
  justify-content: center;
  letter-spacing: 2px;
  display: block;
  background: rgb(255,192,203);
  background: linear-gradient(90deg, rgba(255,192,203,1) 26%, rgb(224,255,255,1) 69%, rgba(224,255,255,1) 69%);
  font-size: 1em;
  font-weight: bold;
  margin-top: 30px;
  outline: none;
  border: none;
  border-radius: 5px;
  transition: .2s ease-in;
  cursor: pointer;
}

.sign-btn:hover {
 background: rgb(149, 235, 235);
}

/* -----------login page--------------- */
.L-container{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: rgb(255,192,203);
  background: linear-gradient(90deg, rgba(255,192,203,1) 26%, rgba(224,255,255,1) 69%, rgba(224,255,255,1) 69%); 
}

.l-main {
  margin: auto;
  margin-top: 60px;
  margin-bottom: 50px;
  background-color: black;
  height: 400px;
  width: 500px;
  overflow: hidden;
  border-radius: 10px;
  box-shadow: 5px 20px 50px #000;
}


.links{
  display: flex;
  margin-top: 60px;
  column-gap: 100px;
}

a {
  text-decoration: none;
  color: white;
  margin-left: 30px;
  font-size: 17px;
}

a:hover {
  border-bottom: 1px solid #ffefba;
}

/* ----------- dashboard update form */
#U-container{
  display: flex;
}
.update-form{
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  border-radius: 10px;
  padding: 30px;
  margin-left: 90px;
  background-color: rgb(187, 212, 212);
}
.update-form input{
  width: 360px;
  height: 40px;
  margin-bottom: 10px;
}
.update-form textarea{
  width: 360px;
  height: 70px;
  margin-bottom: 10px;
}
.update-form button{
  color: #ffffff;
  width: 360px;
  height: 40px;
  margin-top: 10px;
  font-weight: 40px;
  font-size: 20px;
}


@media only screen and (max-width:1000px){
  .l-main{
   width: 350px;
  } 
  .main{
    width: 350px;
   }  
   #U-container{
    display: block;
  }
  .update-form{
    margin-top: 40px;
    width: 330px;
    margin-left: 0px;
    padding: 20px; 
  }
  .update-form input{
    width: 280px;
    height: 40px;
    margin-bottom: 10px;
  }
  .update-form textarea{
    width: 280px;
    height: 70px;
    margin-bottom: 10px;
  }
  .update-form button{
    color: #ffffff;
    width: 280px;
  }
}